<template>
  <div class="course-screen-question">
    <head-top title="截屏提问列表">
      <!--      <router-link slot="right" to="/course/screen-question/edit">-->
      <!--        <van-icon name="plus" size="2.3rem" color="#fff" class="head-right"></van-icon>-->
      <!--      </router-link>-->
    </head-top>

    <van-tabs v-model="selectIndex" animated swipeable @change="typeChanged">
      <van-tab v-for="(item, index) in typeOptions" :key="index" :title="item.typeName">
        <div v-if="item.questionList && item.questionList.length> 0" class="flex-wrap-between sub_module">
          <div v-for="(sub, k) in item.questionList" :key="k" class="image_card van-hairline--surround"
               @click="questionSelect(sub)">
            <div class="list-item">
              <r-image width="42vw" height="30vw" :src="sub.imgSrc" class="item-left">
              </r-image>
              <div class="item-right">
                <div class="flex-between">
                  <span>序号：{{ sub.index }}</span>
                  <van-tag mark plain>{{sub.stateName}}</van-tag>
                </div>
                <div class="flex-between">
                  <span>时长：{{sub.time}}</span>
                  <van-tag plain type="success">{{sub.ModeName}}</van-tag>
                </div>
                <div class="flex-between">
                  <span>分数：{{sub.score}}</span>
                  <span>{{sub.createTime}}</span>
                </div>
              </div>
            </div>
          </div>
          <span style="width: 42vw;"></span>
        </div>
        <van-cell v-else title="暂无数据"></van-cell>
      </van-tab>
    </van-tabs>

  </div>
</template>

<script>
import RImage from "@/components/r-image";
import { ModulePicture, screenPatternName, ScreenSubjectStateName } from "@/utils/enum";
import { formatDate } from "@/utils/formatData";

export default {
  name: "screenQuestion",
  components: { RImage },
  data () {
    return {
      dataList: [],
      typeOptions: [
        { typeName: "单选题", typeValue: 1, questionList: [] },
        { typeName: "多选题", typeValue: 2, questionList: [] },
        { typeName: "判断题", typeValue: 3, questionList: [] },
        { typeName: "问答题", typeValue: 4, questionList: [] }
      ],
      selectIndex: 0,
      typeNames: ['单选题', '多选题', '判断题', '问答题'],
      questionModeNames: ['全员作答', '随机挑人', '抢答']
    };
  },
  mounted () {
    this.typeChanged(0);
  },
  methods: {
    typeChanged (value) {
      let subject = this.typeOptions[value];
      subject.questionList = [];
      this.$gatewayHttp.screenQuestion.getDataList({
        ssType: subject.typeValue
      }).then(res => {
        if (res.Result) {
          if (res.Datas && res.Datas.length > 0) {
            res.Datas.sort((a, b) => {
              return a["CreateTime"] > b["CreateTime"] ? -1 : 1;
            });

            res.Datas.forEach(item => {
              subject.questionList.push({
                id: item["SSID"],
                type: subject.typeValue,
                imgSrc: this.$gatewayHttp.resourse.picture(item["SSContent"], ModulePicture.ScreenSubject),
                state: item["State"],
                stateName: ScreenSubjectStateName(item["State"]),
                time: item["SSTime"],
                score: item["SSScore"],
                optionsNum: item["SSOption"],
                questionMode: item["SSPattern"],
                ModeName: screenPatternName(item["SSPattern"]),
                index: item["SSSerial"],
                answer: item["SSAnswer"],
                survey: item["SSSurvey"],
                createTime: formatDate(item["CreateTime"], "hh:mm:ss")
              });
            });
          }
        } else if (res.Info) {
          this.$toast(res.Info);
        }
      }).catch(() => {
      });
    },
    questionSelect (sub) {
      this.$store.commit("SET_SCREEN_QUESTION", sub);
      this.$router.push("/course/screen-question/view");
    }
  }
}
</script>

<style lang="less" scoped>
@import "./../../../../styles/common/base";
.course-screen-question {
  min-height: 100%;
  background-color: #fff;

  .image_card {
    width: 100%;
  }

  .list-item {
    display: flex;
    align-items: center;
    width: 100%;

    .item-left {
      flex: 1;
    }

    .item-right {
      flex: 2;
      margin: 0 16px;

      .flex-between {
        line-height: 3;
      }
    }
  }
}

</style>
